p {
  font-size: 17px;
  line-height: 1.3em;
}

@media (min-width: 400px) {
  p {
    font-size: 18px;
  }
}

.center {
  text-align: center;
}

.secondary-text {
  color: var(--text_secondary);
}

.block-image {
  display: block;
  margin: 0 auto;
  max-width: 100%;
  max-height: 50vh;
}

.fx-mirror-x {
  transform: scaleX(-1);
}
